import { Component, OnInit } from '@angular/core';
import { LockScreenService, UsersService, ModalUploadService } from 'src/app/service/index.service';
import { UserModel } from 'src/app/models/user.model';

@Component({
	selector: 'modal-lock-screen',
	templateUrl: './lock-screen.component.html',
	styles: []
})
export class LockScreenComponent implements OnInit {
	password: string;
	user: UserModel;
	constructor(
		private _Modalservicas: LockScreenService,
		public _userService: UsersService,
		private _modalService: ModalUploadService) { }

	ngOnInit() {
	}

	iniciarSession() {
		const user = new UserModel(
			'',
			'',
			this._userService.UserData.email,
			this.password,
			''
		);
		this._userService.initSession(user, true).subscribe(resp => {
			if (resp) {
				this._Modalservicas.ocultar();
			} else {
				this._userService.logout();
			}
		});
	}

}
